import React from 'react';
import Home from './components/template/Home';
import Educacion from './components/educacion/Educacion';
import Habilidades from './components/habilidades/Habilidades';
import Experiencia from './components/experiencia/Experiencia';
import Contacto from './components/contacto/Contacto';
import { Link } from 'react-router-dom';

const ROUTES = [
    { exact: true, path: '/portafolio/', component: Home },
    { exact: true, path: '/portafolio/Educacion', component: Educacion },
    { exact: true, path: '/portafolio/Habilidades', component: Habilidades },
    { exact: true, path: '/portafolio/Experiencia', component: Experiencia },
    { exact: true, path: '/portafolio/Contacto', component: Contacto },
    { exact:null, path:null, component: NotFound }
];

function NotFound(){
    return (
        <div>
            <p>sitio no encontrado</p>
            <Link to='/portafolio'>Click para regresar</Link>
        </div>
    )
}

export default ROUTES;